import Axios from "axios";
import { CYBER_TOKEN, DOMAIN, TOKEN } from "../util/settings/config";

export class baseServices {
    //put json v pha backend
    put = (url,model) => {
        return  Axios({
            url:`${DOMAIN}${url}`,
            method:'PUT',
            data:model,
            headers: {'Authorization': 'Bearer ' + (localStorage.getItem(TOKEN)),'TokenCybersoft': CYBER_TOKEN} //JWT
        }) 
    }

    post = (url, model) => {
        return Axios({
            url:`${DOMAIN}${url}`,
            method:'POST',
            data:model,
            headers: {'Authorization': 'Bearer ' + (localStorage.getItem(TOKEN)),'TokenCybersoft': CYBER_TOKEN} //JWT
        }) 
    }


    get = (url) => {
        return Axios({
            url:`${DOMAIN}${url}`,
            method:'GET',
            headers: {'Authorization': 'Bearer ' + (localStorage.getItem(TOKEN)),'TokenCybersoft': CYBER_TOKEN} //token yu cu t backend chng minh user  ng nhp ri
        })
    }

    delete = (url) => {
        return Axios({
            url:`${DOMAIN}${url}`,
            method:'DELETE',
            headers: {'Authorization': 'Bearer ' + (localStorage.getItem(TOKEN)),'TokenCybersoft': CYBER_TOKEN} //token yu cu t backend chng minh user  ng nhp ri
        })
    }
}