import React, { useState } from 'react';
import {
    Form,
    Input,
    DatePicker,
    InputNumber,
    Switch,
} from 'antd';
import * as Yup from 'yup';
import { Formik, useFormik } from 'formik';
import { useDispatch } from 'react-redux';
import { themPhimUploadHinhAction } from '../../../../redux/action/QuanLyPhimAction';
import { GROUPID } from '../../../../util/settings/config';
import moment from 'moment';
import TextArea from 'antd/lib/input/TextArea';

export default function AddFilm() {
    const [imgSrc, setImgSrc] = useState('');
    const dispatch = useDispatch();
    const formik = useFormik({
        initialValues: {
            tenPhim: '',
            trailer: '',
            moTa: '',
            ngayKhoiChieu: '',
            dangChieu: false,
            sapChieu: false,
            hot: false,
            danhGia: 0,
            hinhAnh: {},
        },
        validationSchema: Yup.object().shape({
            tenPhim: Yup.string().required('Tn phim khng c b trng !').min(6, 'Tn phim ti thiu 6 k t !').max(100, 'Tn phim ti a 100 k t'),
            trailer: Yup.string().required('Trailer khng c b trng !').matches(/^((?:https?:)?\/\/)?((?:www|m)\.)?((?:youtube\.com|youtu.be))(\/(?:[\w\-]+\?v=|embed\/|v\/)?)([\w\-]+)(\S+)?$/, 'Trailer khng ng nh dng'),
            moTa: Yup.string().required('M t khng c b trng !').min(10, 'M t ti thiu 10 k t !').max(600, 'M t ti a 600 k t !'),
            // bt li ngy khi chiu ln hn hoc bng ngy hin ti

        }),
        onSubmit: (values) => {
            console.log('values', values);
            values.maNhom = GROUPID;
            //To i tng formdata => a gi tr values t formik vo formdata
            let formData = new FormData();
            for (let key in values) {
                if (key !== 'hinhAnh') {
                    formData.append(key, values[key]);
                } else {
                    formData.append('File', values.hinhAnh, values.hinhAnh.name);
                }
            }
            //Gi api gi cc gi tr formdata v backend x l
            dispatch(themPhimUploadHinhAction(formData));
        }
    })
    const handChangeDatePicker = (value) => {
        let ngayKhoiChieu = moment(value).format('DD/MM/YYYY');
        formik.setFieldValue('ngayKhoiChieu', ngayKhoiChieu);
    }
    const handleChangeSwitch = (name) => {
        return (value) => {
            formik.setFieldValue(name, value)
        }
    }
    const handleChangeInputNumber = (name) => {
        return (value) => {
            formik.setFieldValue(name, value);
        }
    }
    const handleChangeFile = (e) => {
        //Ly file ra t e
        let file = e.target.files[0];
        if (file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/gif' || file.type === 'image/png') {
            //To i tng  c file
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = (e) => {
                // console.log(e.target.result);
                setImgSrc(e.target.result);//Hnh base 64
            }
            //em d liu file lu vo formik
            formik.setFieldValue('hinhAnh', file);
        }
    }

    return (
        <Form
            onSubmitCapture={formik.handleSubmit}
            labelCol={{
                span: 4,
            }}
            wrapperCol={{
                span: 14,
            }}
            layout="horizontal"
        >
            <h3>Thm mi phim </h3>
            <Form.Item label="Tn phim">
                <Input name="tenPhim" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.tenPhim && formik.errors.tenPhim &&
                <div className="alert alert-danger">{formik.errors.tenPhim}</div>}
            </Form.Item>
            <Form.Item label="Trailer">
                <Input name="trailer" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.trailer && formik.errors.trailer &&
                <div className="alert alert-danger">{formik.errors.trailer}</div>}
            </Form.Item>
            <Form.Item label="M t">
                <TextArea name="moTa" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.moTa && formik.errors.moTa &&
                <div className="alert alert-danger">{formik.errors.moTa}</div>}
            </Form.Item>
            <Form.Item label="Ngy khi chiu">
                <DatePicker format={"DD/MM/YYYY"} onChange={handChangeDatePicker} />
            </Form.Item>
            <Form.Item label="ang chiu" >
                <Switch onChange={handleChangeSwitch('dangChieu')} />
            </Form.Item>
            <Form.Item label="Sp chiu">
                <Switch onChange={handleChangeSwitch('sapChieu')} />
            </Form.Item>
            <Form.Item label="Hot">
                <Switch onChange={handleChangeSwitch('hot')} />
            </Form.Item>
            <Form.Item label="nh Gi">
                <InputNumber onChange={handleChangeInputNumber('danhGia')} min={1} max={10} />
            </Form.Item>
            <Form.Item label="Hnh nh">
                <input type="file" onChange={handleChangeFile} accept="image/png, image/jpeg,image/gif,image/png" />
                <br />
                <img style={{ width: 150, height: 150 }} src={imgSrc} alt="..." />
            </Form.Item>
            <Form.Item label="Tc v">
                <button type='submit' style={{ backgroundColor: 'rgb(125 211 252)' }} className='p-2 text-white'>Thm Phim</button>
            </Form.Item>
        </Form>
    )
}
