import { TextField, Button, IconButton, InputLabel, FormControl, FormHelperText } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import { styled } from '@mui/system';
import CancelIcon from '@mui/icons-material/Cancel';
import React, { useState } from 'react'
import { Link } from 'react-router-dom';
import { useFormik } from 'formik';
import * as Yup from 'yup'
import { dangKyAction } from '../../../../redux/action/QuanLyNguoiDungAction';
import { useDispatch } from 'react-redux';


const CssTextField = styled(TextField)({
    "& label": {
        color: "#2196f4"
    },
    "& .MuiOutlinedInput-root": {
        "& fieldset": {
            borderColor: "#2196f4",
            borderWidth: 5
        },
        "&:hover fieldset": {
            borderColor: "#2196f4"
        },
        "&.Mui-focused fieldset": {
            borderColor: "#2196f4",
            borderWidth: 5
        }
    }
});

const CssSelect = styled(FormControl)({
    'label': {
        color: '#2196f4',
    },
    '& .MuiOutlinedInput-root': {
        "& fieldset": {
            borderColor: "#2196f4",
            borderWidth: 5
        },
        "&:hover fieldset": {
            borderColor: "#2196f4"
        },
        "&.Mui-focused fieldset": {
            borderColor: "#2196f4",
            borderWidth: 5
        }
    }
});


export default function Register(props) {
    const [group, setGroup] = useState('GP01');

    const selectGroup = (event) => {
        setGroup(event.target.value);
    };

    const dispatch = useDispatch();

    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            hoTen: '',
            email: '',
            soDt: '',
            maNhom: 'GP01'
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng !').trim(),
            matKhau: Yup.string().required('Mt khu khng c b trng !').trim().min(6, 'Mt ti thiu 6 k t').max(32, 'Mt khu ti a 32 k t'),
            email: Yup.string().required('Email khng c b trng !').trim().email('Email khng ng nh dng'),
            soDt: Yup.string().required('S in thoi khng c b trng !').trim().matches(/^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im,'S in thoi khng ng nh dng'),
            hoTen: Yup.string().required('H tn khng c b trng !').trim(),
        }),
        onSubmit: (values) => {
            const action = dangKyAction(values);
            dispatch(action)
            // console.log('values', values);
        }
    })

    return (
        <div className="register">
            <div className="register__content col-12 col-sm-7 mx-auto px-5">
                <Link to="/" >
                    <IconButton className="register__close nav-link" aria-label="delete" color="error" size="large">
                        <CancelIcon fontSize="large" />
                    </IconButton>
                </Link>
                <div className="register__logo">
                    <img src="./img/logo.png" alt="" />
                </div>
                <div className="register__form">
                    <p className="text-center">ng k  c nhiu u i, mua v v bo mt thng tin!</p>
                    <form onSubmit={formik.handleSubmit}>
                        <div className="register__field row">
                            <div className="field--col col-12 col-sm-6">
                                <div className="field--row">
                                    <CssTextField
                                        label="Ti khon"
                                        id="taiKhoan"
                                        fullWidth
                                        variant="outlined"
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        helperText={(formik.touched.taiKhoan) && (formik.errors.taiKhoan) ? formik.errors.taiKhoan : ""}
                                        error={(formik.touched.taiKhoan) && (formik.errors.taiKhoan) ? true : false}
                                    />
                                </div>
                                <div className="field--row">
                                    <CssTextField
                                        label="Email"
                                        id="email"
                                        fullWidth
                                        variant="outlined"
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        helperText={(formik.touched.email) && (formik.errors.email) ? formik.errors.email : ""}
                                        error={(formik.touched.email) && (formik.errors.email) ? true : false}
                                    />
                                </div>
                                <div className="field--row">
                                    <CssTextField
                                        label="H tn"
                                        id="hoTen"
                                        fullWidth
                                        variant="outlined"
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        helperText={(formik.touched.hoTen) && (formik.errors.hoTen) ? formik.errors.hoTen : ""}
                                        error={(formik.touched.hoTen) && (formik.errors.hoTen) ? true : false}
                                    />
                                </div>
                            </div>
                            <div className="field--col col-12 col-sm-6">
                                <div className="field--row">
                                    <CssTextField
                                        label="Mt khu"
                                        id="matKhau"
                                        type="password"
                                        fullWidth
                                        variant="outlined"
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        helperText={(formik.touched.matKhau) && (formik.errors.matKhau) ? formik.errors.matKhau : ""}
                                        error={(formik.touched.matKhau) && (formik.errors.matKhau) ? true : false}
                                    />
                                </div>
                                <div className="field--row">
                                    <CssTextField
                                        label="S in thoi"
                                        id="soDt"
                                        fullWidth
                                        variant="outlined"
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        helperText={(formik.touched.soDt) && (formik.errors.soDt) ? formik.errors.soDt : ""}
                                        error={(formik.touched.soDt) && (formik.errors.soDt) ? true : false}
                                    />
                                </div>
                                <div className="field--row">
                                    <CssSelect
                                        fullWidth
                                    >
                                        <InputLabel id="maNhomLabel">M nhm</InputLabel>
                                        <Select
                                            labelId="maNhomLabel"
                                            id="maNhom"
                                            value={group}
                                            label="M nhm"
                                            onChange={selectGroup}
                                        >
                                            <MenuItem value={"GP01"}>GP01</MenuItem>
                                            <MenuItem value={"GP02"}>GP02</MenuItem>
                                            <MenuItem value={"GP03"}>GP03</MenuItem>
                                            <MenuItem value={"GP04"}>GP04</MenuItem>
                                        </Select>
                                         <FormHelperText>{(formik.touched.maNhom) && (formik.errors.maNhom) ? formik.errors.maNhom : ""}</FormHelperText>
                                    </CssSelect>
                                </div>



                            </div>
                        </div>
                        <div className="register__button">
                            <Link to="/login"  className="button--login">
                                <Button variant="contained">NG NHP</Button>
                            </Link>
                            <Button type="submit" variant="contained">NG K</Button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
