import React, { useEffect, useState } from 'react';
import {
    Form,
    Input,
    Select,
    Button
} from 'antd';
import * as Yup from 'yup';
import { Formik, useFormik } from 'formik';
import { useSelector, useDispatch } from 'react-redux';
import { GROUPID } from '../../../../util/settings/config';
import { layThongTinLoaiNguoiDungAction, themNguoiDungAction } from '../../../../redux/action/QuanLyNguoiDungAction';

export default function AddUsers(props) {
    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            maNhom: '',
            maLoaiNguoiDung: '',
            hoTen: ''
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string().required('Ti khon khng c b trng!').matches(/^(?![_.])(?!.*[_.]{2})[a-zA-Z0-9._]+(?<![_.])$/,'Ti khon khng ng nh dng !').min(6,'Ti khon ti thiu 6 k t !').max(20,'Ti khon ti a 20 k t !'),
            matKhau: Yup.string().required('Mt khu khng c b trng !').matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,20}$/,'Mt khu khng ng nh dng !'),
            email: Yup.string().required('Email khng c b trng !').email('Email khng ng nh dng !'),
            soDt: Yup.string().required('ST khng c b trng !').matches(/(84|0[3|5|7|8|9])+([0-9]{8})\b/,'ST khng ng inh dng !'),
            hoTen: Yup.string().required('H tn khng c b trng !').matches(/[^a-z0-9A-Z_]/u,'Tn khng ng nh dng !'),
        }),
        onSubmit: (values) => {
            // console.log('values', values);
            values.maNhom = GROUPID;
            dispatch(themNguoiDungAction(values));
        }
    })
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(layThongTinLoaiNguoiDungAction());
    },[]) 
    const { maLoaiNguoiDung } = useSelector(state => state.QuanLyNguoiDungReducer);
    const handleChangeMaNguoiDung = (value) => {
        formik.setFieldValue('maLoaiNguoiDung', value)
    }
    const convertSelectMND = () => {
        return maLoaiNguoiDung?.map((mnd, index) => {
            return { label: mnd.tenLoai, value: mnd.maLoaiNguoiDung }
        })
    }
    return (
        <Form
            onSubmitCapture={formik.handleSubmit}
            labelCol={{
                span: 4,
            }}
            wrapperCol={{
                span: 14,
            }}
            layout="horizontal"
        >
            <h3>Thm Ngi Dng </h3>
            <Form.Item label="Ti Khon">
                <Input name="taiKhoan" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.taiKhoan && formik.errors.taiKhoan &&
                <div className="alert alert-danger">{formik.errors.taiKhoan}</div>}
            </Form.Item>
            <Form.Item label="Mt Khu">
                <Input name="matKhau" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.matKhau && formik.errors.matKhau &&
                <div className="alert alert-danger">{formik.errors.matKhau}</div>}
            </Form.Item>
            <Form.Item label="Email">
                <Input name="email" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.email && formik.errors.email &&
                <div className="alert alert-danger">{formik.errors.email}</div>}
            </Form.Item>
            <Form.Item label="S in Thoi">
                <Input name="soDt" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.soDt && formik.errors.soDt &&
                <div className="alert alert-danger">{formik.errors.soDt}</div>}
            </Form.Item>
            <Form.Item label="H Tn">
                <Input name="hoTen" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.hoTen && formik.errors.hoTen &&
                <div className="alert alert-danger">{formik.errors.hoTen}</div>}
            </Form.Item>
            <Form.Item name="maLoaiNguoiDung" label="M loi ngi dng">
                <Select placeholder="Chn loi ngi dng" options={convertSelectMND()} onChange={handleChangeMaNguoiDung} onBlur={formik.handleBlur} >
                </Select>
            </Form.Item>
            <Form.Item label="Tc v">
                <Button htmlType='submit'>Thm Users</Button>
            </Form.Item>
        </Form>
    )
}
